<template>
  <el-drawer
      ref="drawer"
      v-model="dialog"
      title="设计图上传器"
      :before-close="close"
      direction="rtl"
      custom-class="demo-drawer">
    <div class="demo-drawer__content">
      <div class="demo-drawer__footer">
        <addshuangchuanqi />
      </div>
    </div>
  </el-drawer>
</template>

<script>
import { ElMessageBox } from 'element-plus'
import addshuangchuanqi from "@/views/sucaiguanli/shejituliebiao/modules/funmodules/addshuangchuanqi";

export default {
  name:'addshejitu',
  data(){
    return {
      table: false,
      loading: false,
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
      },
      formLabelWidth: '80px',
      timer: null,
    }
  },
  props: {
    dialog: {
      type: Boolean,
      default: false,
    }
  },
  methods:{
    handleClose(done){
      if (this.loading) {
        return
      }
      ElMessageBox.confirm('Do you want to submit?')
          .then(() => {
            this.loading = true
            this.timer = setTimeout(() => {
              done()
              // 动画关闭需要一定的时间
              setTimeout(() => {
                this.loading = false
              }, 400)
            }, 2000)
          })
          .catch(() => {
            this.$emit('dialog',false)
          })
    },
    cancelForm(){
      this.loading = false
      clearTimeout(this.timer)
      this.$emit('dialog',false)
    },
    close(){
      this.$emit('dialog',false)
    }
  },
  components:{
    addshuangchuanqi
  }
}
</script>