<template>
  <div class="main">
    <div class="filter">
      <div>
        <el-select v-model="value" placeholder="风格筛选功能开发中" disabled>
        </el-select>
      </div>
      <div>
        <el-select v-model="value" placeholder="标签筛选功能开发中" disabled>
        </el-select>
      </div>
      <div>
        <el-select v-model="value" placeholder="布料筛选功能开发中" disabled>
        </el-select>
      </div>
      <div>
        上传时间：
        <el-date-picker
            v-model="updateDate"
            type="daterange"
            range-separator="到"
            start-placeholder="最早上传时间"
            end-placeholder="最晚上传时间"
        >
        </el-date-picker>
      </div>
      <div>
        <el-switch
            v-model="sort"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text="正序"
            inactive-text="倒序"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "liebiaoshaixuan",
  data(){
    return {
      value:''
    }
  },
  computed: {
    updateDate: {
      get() {
        return this.$store.state.ModuleShejitu.UploadDate
      },
      set(newVal) {
        this.$store.commit('ModuleShejitu/SetUploadDate', newVal)
        this.$store.dispatch('ModuleShejitu/syncGetImgList')
      }
    },
    sort: {
      get() {
        return this.$store.state.ModuleShejitu.ListSort
      },
      set(newVal) {
        this.$store.commit('ModuleShejitu/SetSort', newVal)
        this.$store.dispatch('ModuleShejitu/syncGetImgList')
      }
    }
  },
}
</script>

<style scoped>

.main {
  height: 100%;
  margin-top: 8px;
}
.filter {
  display: table;
}
.filter > div {
  display: table-cell;
  vertical-align: middle;
}
</style>