<template>
    <el-pagination
        v-model:currentPage="Page"
        :page-sizes="$store.state.ModuleShejitu.PageSizes"
        v-model:page-size="PageSize"
        layout="sizes, prev, pager, next, jumper"
        :total="$store.state.ModuleShejitu.Count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
    >
    </el-pagination>
</template>

<script>
import { ref } from 'vue';
export default {
  name: "liebiaofenye",
  data(){
    return{
      currentPage4: ref(1),
    }
  },
  computed: {
    Page: {
      get() {
        return this.$store.state.ModuleShejitu.Page
      },
      set(newVal) {
        this.$store.commit('ModuleShejitu/SetPage', newVal)
        this.$store.dispatch('ModuleShejitu/syncGetImgList')
      }
    },
    PageSize: {
      get() {
        return this.$store.state.ModuleShejitu.PageSize
      },
      set(newVal) {
        this.$store.commit('ModuleShejitu/SetPageSize', newVal)
        this.$store.dispatch('ModuleShejitu/syncGetImgList')
      }
    }
  },
  methods:{
    handleSizeChange:(val) => {
      console.log(val);
    },
    handleCurrentChange:(val) => {
      console.log(`current page: ${val}`)
    }
  }
}
</script>

<style scoped>
div {
  height: 100%;
}
</style>